import React,{useContext} from 'react'
import { FaBars, FaEllipsisV } from 'react-icons/fa'
import {ActiveContext} from './Home'

const Navbar = () => {
    const {active, setActive, toggleInfo, setToggleInfo} = useContext(ActiveContext)
    return (
        <nav className={`navbar ${toggleInfo? 'show-info':'hide-info'} ${active? 'hide-menu':''}`}>
            <FaBars onClick={()=>setActive(true)} />
            <FaEllipsisV onClick={()=>setToggleInfo(!toggleInfo)} />
        </nav>
    )
}

export default Navbar
