import React, {useContext} from 'react'
import {ActiveContext} from './Home'
import Experience from './Experience'
import Project from './Project'
import { FaFacebook, FaGithub, FaInstagram, FaNodeJs, FaLinkedin, FaPython, FaReact, FaRegCopyright, FaTwitter, FaVuejs } from 'react-icons/fa'
import { SiGraphql, SiMongodb, SiMysql, SiGatsby, SiDjango, SiTypescript } from 'react-icons/si'
import {DiJavascript} from 'react-icons/di'
import Form from './Form'
import {Experiences, Education} from '../../data/ExperienceData'
import {Projects} from '../../data/ProjectsData'




const Content = () => {
    const {active} = useContext(ActiveContext)
    
    return (
        <div className={`content ${active? "active":""}`}>
            <div className="content-wrapper">
                <div className="top-bg">
                    <div className="top-bg-overlay">

                        <div className="main-bg-wrapper">
                            
                            <div className="main-bg">
                                <div className="main-bg-overlay">
                                    <div className="title">
                                        <h3>Hello! I'm Samuel</h3>
                                        <h4>Software Developer</h4>
                                        <p>Frontend <span>|</span> Backend <span>|</span> Mobile</p>

                                        <div className="buttons">
                                            <a href="#resume" className="bg-button">View Resume</a>
                                        </div>           
                                        
                                    </div>

                                </div>
                            </div>
                        </div>
                    </div> 
                </div>
                
                <section className="section-skills">
                    <div className="section-wrapper">
                        <h4>My Tools and Languages:</h4>
                            <div className="skills-container">
                                <div className="skill"><DiJavascript /><span>Javascript</span></div>
                                <div className="skill"><SiTypescript /><span>Typescript</span></div>
                                <div className="skill"><FaPython /><span>Python</span></div>
                                <div className="skill"><FaReact /><span>ReactJS</span></div>
                                <div className="skill"><FaVuejs /><span>VueJS</span></div>
                                <div className="skill"><FaNodeJs /><span>NodeJS</span></div>
                                <div className="skill"><FaVuejs /><span>NextJS</span></div>
                                <div className="skill"><SiGatsby /><span>GatsbyJS</span></div>
                                <div className="skill"><SiDjango /><span>Django</span></div>
                                <div className="skill"><SiMysql /><span>SQL DBs</span></div>
                                <div className="skill"><SiMongodb /><span>Mongodb</span></div>
                                <div className="skill"><SiGraphql /><span>Graphql</span></div>
                            </div>
                    </div>
                </section>

                <section className="socials">
                    <div className="socials-wrapper">
                        <h4>Socials:</h4>
                        <div className="socials-container">
                            <a href="https://github.com/samoffem" target="_blank" rel="noreferrer" className="social"><FaGithub /><span>Github</span></a>
                            <a href="https://www.linkedin.com/in/samuel-offem-165a72171" target="_blank" rel="noreferrer" className="social"><FaLinkedin /><span>LinkedIn</span></a>
                            <a href="https://www.twitter.com/_samwell__" target="_blank" rel="noreferrer" className="social"><FaTwitter /><span>Twitter</span></a>
                            <a className="social"><FaFacebook /><span>Facebook</span></a>
                            <a className="social"><FaInstagram /><span>Instagram</span></a>
                        </div>
                    </div>
                </section>

                <section className="projects">
                    <div className="projects-header">
                        <h4>Selected Personal Projects:</h4>
                    </div>
                    <div className="projects-container">
                        {Projects.map((data, index)=> <Project data={data} key={index} />)}
                    </div>
                </section>       

                <section className="work-education" id="resume">
                    <div className="we-wrapper">

                        <div className="work-wrapper">
                            <h4>Experience</h4>
                            <div className="work">
                                {Experiences.map((data,index)=> <Experience data={data} key={index} />)}
                                
                            </div>
                           
                        </div>

                        <div className="education-wrapper">
                            <h4>Education</h4>
                            <div className="education">
                                {Education.map((data, index)=> <Experience data={data} key={index} />)}
                                
                            </div>
                            
                        </div>

                    </div>
                </section>

                <section className="message">
                    <Form />
                </section>
                
                <footer>
                    <div className="footer-container">
                        <p>Designed by Samuel Offem</p>
                        <p><FaRegCopyright /> <span>samoffem@gmail.com 2021</span></p>
                    </div>
                </footer>
            </div>
            
        </div>
    )
}

export default Content
