import React,{useContext} from 'react'
import avatar from '../../assets/img-5.jpg'
import {FaTwitter, 
    FaFacebook, 
    FaGithub, 
    FaLinkedin, 
    FaInstagram,  
    FaDownload,
} from 'react-icons/fa'
import { IconContext } from 'react-icons'
import {ActiveContext} from './Home'

const InfoBar = () => {
    const {toggleInfo} = useContext(ActiveContext)

    return (
        <aside className={`info-bar ${toggleInfo? 'active':''}`}>
            <div className="info-bar-wrapper">
                <div className="info-bar-container">

                    <div className="info-bar-header">
                        <div className="info-bar-img">
                            <img src={avatar} className="avatar" />
                            <div className="live"></div>
                        </div>
                        <h5 className="avatar-name">Samuel Offem</h5>
                        <div className="description">
                            <h5>Software Developer</h5>
                            <h5>Fullstack Developer</h5>
                        </div>
                    </div>

                    {/* Info Details */}
                    <div className="info-bar-details">
                        <div className="basic-info">
                            <ul>
                                <li><h5>Country:</h5><span>Nigeria</span></li>
                                <li><h5>City:</h5><span>Lagos</span></li>
                                <li><h5>Email:</h5><span>samoffem@gmail.com</span></li>
                            </ul>
                        </div>
                        <div className="divider"></div>
                    
                        <div className="skills">
                            <h5>Specialties:</h5>
                            <ul className="top-skills">
                                <li className="skill-item">HTML/CSS</li>
                                <li className="skill-item">Javascript</li>
                                <li className="skill-item">Python</li>
                                <li className="skill-item">ReactJS</li>
                                <li className="skill-item">VueJS</li>
                                <li className="skill-item">NodeJS</li>
                                <li className="skill-item">NextJS</li>
                                <li className="skill-item">GatsbyJS</li>
                                <li className="skill-item">Django</li>
                            </ul>
                        </div>
                        <div className="divider"></div>

                        <div className="download-resume">
                            <a>Download My CV <FaDownload size="12px" /></a>
                        </div>
                    </div>

                    <div className="info-bar-footer">

                        <IconContext.Provider value={{size:'15'}}>
                            <div className="social-icon">
                                <FaInstagram />
                            </div>

                            <div className="social-icon">
                                <FaTwitter />
                            </div>

                            <div className="social-icon">
                                <FaFacebook />
                            </div>

                            <div className="social-icon">
                                <FaLinkedin />
                            </div>

                            <div className="social-icon">
                                <FaGithub />
                            </div>
                        </IconContext.Provider>
                    </div>

                </div>
            </div>
        </aside>
    )
}

export default InfoBar
